"use client";
import React from 'react';
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid } from 'recharts';

const data = [
  { date: 'Feb', value: 11300000 },
  { date: 'Mar', value: 12100000 },
  { date: 'Mar 15', value: 12450000 },
  { date: 'Apr', value: 12770000 },
  { date: 'Apr 30', value: 12770000 },
];

export default function PortfolioActivityChart() {
  return (
    <div className="bg-white rounded-lg shadow-sm border border-gray-200 p-6 flex flex-col">
      <div className="flex items-center justify-between mb-2">
        <h2 className="text-lg font-semibold text-gray-800">Portfolio Activity Summary</h2>
        <div className="flex gap-1">
          {["1D","1W","1M","3M","YTD","1Y","ALL"].map((r) => (
            <button key={r} className="px-2 py-1 text-xs rounded bg-gray-100 hover:bg-gray-200 font-medium text-gray-600">{r}</button>
          ))}
        </div>
      </div>
      <div className="h-48 w-full">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={data} margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="date" tick={{ fontSize: 12 }} />
            <YAxis tickFormatter={v => `$${(v/1e6).toFixed(1)}M`} tick={{ fontSize: 12 }} />
            <Tooltip formatter={v => `$${v.toLocaleString()}`} />
            <Line type="monotone" dataKey="value" stroke="#6366f1" strokeWidth={2} dot={false} />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
}
